import { Component, OnInit, Input } from '@angular/core';
import * as $ from 'jquery';


@Component({
    selector: 'text-animation',
    templateUrl: './textAnimation.html',
    styleUrls: ['./textAnimation.css']
})
export class textAnimationComponent implements OnInit {
    title = 'textproject';
    aText = [];
    iSpeed = 100; // time delay of print out
    iIndex = 0; // start printing array at this posision
    iArrLength = 0; // the length of the text array
    iScrollAt = 20; // start scrolling up at this many lines

    iTextPos = 0; // initialise text position
    sContents = ''; // initialise contents variable
    iRow; // initialise currents row
	@Input() childMessage: string;
	@Input() styleType: string;
	
    constructor(){
		this.childMessage = this.childMessage;
		this.styleType = this.styleType;
    }

    ngOnInit() {
		this.aText.push(this.childMessage)
		this.iArrLength =  this.aText[0].length;
		switch (this.styleType){
			case "underline":
				this.textTypeAnimation();
				this.textUnderlineSvg();
				break;
			case "underlinesvg":
				this.textUnderlineSvg();
				break;
			case "stripe":
				this.textStripeAnimation()
				break;
			default:
				this.textTypeAnimation();
		}
        
		
		console.log(this.styleType)
    }


    textTypeAnimation= () => {
        this.sContents = ' ';
        this.iRow = Math.max(0, this.iIndex - this.iScrollAt);
        let destination = document.getElementById(this.styleType);

        while (this.iRow < this.iIndex) {
            this.sContents += this.aText[this.iRow++] + '<br />';
        }
        destination.innerHTML = this.sContents + this.aText[this.iIndex].substring(0, this.iTextPos) + "";
        if (this.iTextPos++ == this.iArrLength) {
            this.iTextPos = 0;
            this.iIndex++;
            if (this.iIndex != this.aText.length) {
                this.iArrLength = this.aText[this.iIndex].length;
                setTimeout(this.textTypeAnimation, 500);
            }
			destination.className = this.styleType;
        } else {
            setTimeout(this.textTypeAnimation, this.iSpeed);
        }
    }

	textUnderlineSvg(){
		$(function() {
		  var text = $(".textanim");
		   text.removeClass("hidden");
		  $(window).scroll(function() {
			var scroll = $(window).scrollTop();

			if (scroll >= 200) {
			  text.removeClass("hidden");
			} else {
			  text.addClass("hidden");
			}
		  });
		});
	}

	textStripeAnimation () {
		console.log("no animation")
	}
	
}
